import storage from '@/api/storage.js';
import utilCore from '@/api/utilCore.js';
    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
		components: {},
		data(){
			return{
				id:1,
				userEn:null  ,//我的数据
				isLogin:false
			}
		},
		onLoad(){
			this.userEn = storage.getMyInfo();
			this.isLogin = utilCore.isLogin();
		},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {	
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                storage.outLogin();
                uni.reLaunch({
                    url: '../login/login'
                });
            },
			revise(){
				uni.navigateTo({
					url:'../MyInfo/MyInfo'
				})
			}
        }
    }
